<template>
    <view>
        hello{{apkKist}}
    </view>
</template>

<script>
    export default {
        data() {
            return {
                title: 'Hello',
                appList: [],
                apkKist: []
            }
        },
        onLoad() {
            this.ggg()
        },
        methods: {
            getAppList() {
                plus.android.importClass('java.util.ArrayList');
                plus.android.importClass('android.content.pm.PackageInfo');
                plus.android.importClass('android.content.pm.PackageManager');
                var ApplicationInfo = plus.android.importClass('android.content.pm.ApplicationInfo');
                var MainActivity = plus.android.runtimeMainActivity();
                var PackageManager = MainActivity.getPackageManager();
                var pinfo = plus.android.invoke(PackageManager, 'getInstalledPackages', 0)
                if (pinfo != null) {
                    this.appList = []
                    for (var i = 0; i < pinfo.size(); i++) {
                        var pkginfo = pinfo.get(i);
                        var issysapk = ((pkginfo.plusGetAttribute("applicationInfo").plusGetAttribute("flags") &
                            ApplicationInfo.FLAG_SYSTEM) != 0) ? true : false
                        if (issysapk == false) {
                            const apkinfo = {
                                appName: pkginfo.plusGetAttribute("applicationInfo").loadLabel(PackageManager)
                                    .toString(),
                                packageName: pkginfo.plusGetAttribute("packageName"),
                                versionName: pkginfo.plusGetAttribute("versionName"),
                                versionCode: pkginfo.plusGetAttribute("versionCode")
                            }
                            this.appList.push(apkinfo)
                        }

                    } //打印出所有的APP名称，包名，版本
                    // 　　console.log(JSON.stringify(this.appList))
                }
            },
            ggg() {
                var ApplicationInfo = plus.android.importClass('android.content.pm.ApplicationInfo');
                var MainActivity = plus.android.runtimeMainActivity();
                var PackageManager = MainActivity.getPackageManager();
                var pinfo = plus.android.invoke(PackageManager, 'getInstalledPackages', 0)
                if (pinfo != null) {
                    var apklist = []
                    for (var i = 0; i < pinfo.size(); i++) {
                        //PackageInfo{4b45699f9d com.tencent.mobileqq}
                        var pkginfo = pinfo.get(i);
                        var issysapk = ((pkginfo.plusGetAttribute("applicationInfo").plusGetAttribute("flags") &
                            ApplicationInfo.FLAG_SYSTEM) != 0) ? true : false
                        if (issysapk == false) {
                            const apkinfo = {
                                appName: pkginfo.plusGetAttribute("applicationInfo").loadLabel(PackageManager)
                                    .toString(), //应用名称
                                appIco: pkginfo.plusGetAttribute("applicationInfo").loadIcon(PackageManager),
                                packageName: pkginfo.plusGetAttribute("packageName"), // 包名
                                versionName: pkginfo.plusGetAttribute("versionName"), //版本号
                                versionCode: pkginfo.plusGetAttribute("versionCode") // 版本Code
                    }
     }
                            var bimp = apkinfo.appIco.getBitmap();
                            //console.log("当前bitmap："+bimp);
                            var BitmapFactory = plus.android.importClass("android.graphics.BitmapFactory");
                            var Base64 = plus.android.importClass("android.util.Base64");
                            var Bitmap = plus.android.importClass('android.graphics.Bitmap');
                            var ByteArrayOutputStream = plus.android.importClass("java.io.ByteArrayOutputStream");
                            try {
                                var baos = new ByteArrayOutputStream();
                                bimp.compress(Bitmap.CompressFormat.JPEG, 100, baos);
                                baos.flush();
                                baos.close();
                                var bitmapBytes = baos.toByteArray();
                                var result = Base64.encodeToString(bitmapBytes, Base64.DEFAULT);
                                document.getElementById("showImg").src = "data:image/jpeg;base64," + result;
                            } catch (e) {
                                console.log(e);
                            }
                            apklist.push(apkinfo);
                        }

                    }
                }
            }
        }
</script>

<style>
    .content {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    .logo {
        height: 200rpx;
        width: 200rpx;
        margin-top: 200rpx;
        margin-left: auto;
        margin-right: auto;
        margin-bottom: 50rpx;
    }

    .text-area {
        display: flex;
        justify-content: center;
    }

    .title {
        font-size: 36rpx;
        color: #8f8f94;
    }
</style>
